html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  background-color: #212f39;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Inter, Arial;
  display: flex;
  position: relative;
  overflow-y: hidden;
}

p {
  color: #f8f8ff;
}

a {
  color: gold;
  font-size: 1.5em;
  text-decoration: none;
  transition: color .3s ease-in-out;
  position: absolute;
  top: 4%;
  left: 10%;
}

a:hover {
  color: #0af;
}

.form {
  width: 80%;
  background-color: #3d4c5a;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-top: 10vh;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.form label {
  color: rgba(255, 217, 0, .863);
  flex-direction: column;
  margin-bottom: 10px;
  display: flex;
}

.form input {
  width: 100%;
  color: gold;
  text-align: center;
  background-color: #6b7f94;
  border-radius: 5px;
  outline: none;
  margin-top: 5px;
  padding: 10px;
  font-size: 1.5em;
}

.form button {
  cursor: pointer;
  border: 1 px solid gold;
  color: #ffc400;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 11px;
  margin-top: 20px;
  padding: 12px 20px;
  font-family: inherit;
  font-size: 1.2em;
  font-weight: 400;
  line-height: 24px;
  transition: color .2s ease-in-out;
}

.form button:hover {
  color: #fff;
}

.form button:active {
  color: #5f0;
}

